@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --text: #f7f7ff;
  --banner: #7d83ff;
  --background: #f7f7ff;
  --header-text: #7d83ff;
  --main-text: #272727;
}

#root,
.App {
  min-height: 100vh;
}

#root {
  background: #f7f7ff;
  background: var(--background);
  transition: background 100ms;
}

html,
body {
  background: #f7f7ff;
  background: var(--background);
  transition: background 100ms;
  font-family: "Alata", sans-serif;
  margin: 0;
  padding: 0;
  text-transform: lowercase;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

h1,
p {
  margin: 0;
  padding: 0;
  vertical-align: middle;
  transition-duration: 100ms;
  transition-timing-function: cubic-bezier(0.1, 0, 0.1, 1);
}

a,
a:link,
a:visited,
a:focus,
a:hover,
a:active {
  text-decoration: none;
}
.Header {
  transition: background 100ms cubic-bezier(0.1, 0, 0.1, 1);
  position: absolute;
  width: 100%;
}

.header-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 98%;
  margin: 0 auto;
  transition: background 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.header-title h1 {
  color: var(--text);
  margin: 0.5rem 0 0.6rem 2rem;
  font-size: 2rem;
}

.icon-menu svg,
.card svg {
  transition-duration: 100ms;
  transition-timing-function: cubic-bezier(0.1, 0, 0.1, 1);
}

.icon-menu {
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.banner {
  background: var(--banner);
  display: flex;
  width: 98vw;
  height: 36rem;
  margin: 0 auto;
  align-items: center;
  overflow: hidden;
  border-bottom-left-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  transition: background 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.banner img {
  width: 16rem;
  height: 16rem;
  padding: 1rem;
  border-radius: 50%;
  transition: border 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.banner-image {
  margin-left: 4rem;
  border-radius: 50%;
  border: 0.6rem solid var(--text);
  transition: border 100ms cubic-bezier(0.1, 0, 0.1, 1),
    transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.banner-title {
  color: var(--text);
  margin-left: 4rem;
}

.banner-title h1 {
  font-size: 6rem;
  text-align: center;
}

.banner-title p {
  font-size: 2rem;
}
.card-container {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  margin: 2rem 0;
}

.card {
  display: flex;
  align-items: center;
  justify-content: left;
  background: var(--main-text);
  margin: 0.4rem;
  padding: 1.4rem;
  min-width: 16rem;
  max-width: 16rem;
  border: 0.2rem solid var(--background);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.4rem;
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    background 100ms cubic-bezier(0.1, 0, 0.1, 1),
    border 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1),
    background 100ms cubic-bezier(0.1, 0, 0.1, 1),
    border 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1),
    background 100ms cubic-bezier(0.1, 0, 0.1, 1),
    border 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.card svg {
  margin-right: 1rem;
}

.card h1 {
  color: var(--background);
  font-size: 2rem;
}

.card p {
  color: var(--background);
  font-size: 1rem;
}

.card.card-small {
  flex-grow: 1;
}

.card.card-normal {
  flex-grow: 2;
}

.card.card-large {
  flex-grow: 4;
}

.card:hover,
.icon-menu:hover {
  transform: translateY(-0.1rem);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.card:active,
.icon-menu:active {
  transform: translateY(0.1rem);
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1),
    filter 100ms cubic-bezier(0.1, 0, 0.1, 1),
    -webkit-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
}
@media only screen and (max-width: 1080px) {

  #root,
  .App {
    min-height: 100vh;
  }

  .banner {
    padding-top: 6rem;
    flex-flow: column;
    width: 100%;
    border-bottom-left-radius: 0rem;
    border-bottom-right-radius: 0rem;
    height: 26rem;
  }

  .banner-title {
    margin-left: 0;
  }

  .banner img {
    width: 12rem;
    height: 12rem;
  }

  .banner-image {
    margin-left: 0;
    margin-bottom: 2rem;
  }

  .banner-title h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .banner-title p {
    font-size: 2rem;
    text-align: center;
  }
}
.pfl-container {
  margin: auto;
  width: 100%;
}

.pfl-header {
  color: var(--main-text);
  font-size: 3rem;
  margin: 0 auto 2rem;
  text-align: center;
}

.pfl-title,
.pfl-subtitle {
  color: var(--text);
}

.pfl-card {
  width: 90%;
  margin: 0 auto 2rem;
  color: var(--text);
  border-radius: 0.2rem;
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1)
}

.pfl-card-inner {
  padding: 2rem 2rem;
  border-radius: 0.2rem;
}

.pfl-card-inner {
  -webkit-backdrop-filter: sepia(1) hue-rotate(190deg) brightness(20%);
          backdrop-filter: sepia(1) hue-rotate(190deg) brightness(20%);
  transition: -webkit-backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1), -webkit-backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.pfl-card-inner:hover {
  -webkit-backdrop-filter: sepia(0) hue-rotate(0deg);
          backdrop-filter: sepia(0) hue-rotate(0deg);
  transition: -webkit-backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
  transition: backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1), -webkit-backdrop-filter 100ms cubic-bezier(0.1, 0, 0.1, 1);
}

.pfl-card h1 {
  font-size: 3rem;
  padding-bottom: 1rem;
}

.pfl-card p {
  padding-bottom: 0.4rem;
}

.pfl-card:hover {
  transform: translateY(-0.1rem);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.1);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1)
}

.pfl-card:active {
  transform: translateY(0.1rem);
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  transition: transform 100ms cubic-bezier(0.1, 0, 0.1, 1),
    box-shadow 100ms cubic-bezier(0.1, 0, 0.1, 1)
}
